#home-page video {
  height: 100%;
  width: 100%;
}

#home-splash {
  background-color: white;
}

#home-splash-text {
  position: absolute;
  top: 70%;
  right: 45%;
  
}

#home-splash-text h2 {
  font-size: 32px;
  font-weight: lighter;
  color: white;
}

#home-splash-btn {
  background-color: white;
  border: none;
  border-radius: 5px;
  padding: 12px;
  color: white;
  font-size: 20px;
}

#home-splash-btn a {
  text-decoration: none !important;
    color: #006699;
}

#home-splash-btn:hover {
  background-color: silver;
  transition: 0.5s ease;
}

#choose-us {
  display: flex;
  align-items: center;
  background-color: #006699;
  padding: 75px;
}

#choose-us-text {
  color: white;
  padding: 25px;
  width: 50%;
}

#choose-us-text h3 {
  font-size: 40px;
  font-weight: lighter;
}


#choose-us-text p {
  font-size: 25px;
}

#choose-us-text h4 {
  font-size: 25px;
  font-weight: normal;
}

#choose-us-text li {
  font-size: 20px;
  
}

#choose-us-btn {
  background-color: white;
  border: none;
  border-radius: 5px;
  padding: 12px;
  color: white;
  font-size: 20px;
  margin-top: 50px;
}

#choose-us-btn a {
  text-decoration: none !important;
    color: #006699;
}

#choose-us-btn:hover {
  background-color: silver;
  transition: 0.5s ease;
}

#choose-us-image {
  height: 800px;
  width: 600px;
  padding: 50px;
  
}

#our-services {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.75)), url(../../assets/images/tom-carroll-_oVzCtnZ-eo-unsplash.webp);
  background-position: center;
  background-repeat: no-repeat; 
  background-size: cover;
}

#our-services h3 {
  font-size: 44px;
  color: #006699;
  /* border: 2px solid;
  border-radius: 10px; */
  padding: 20px !important;
  font-weight: lighter;
}

#our-services-cards {
  display: flex;
justify-content: space-between !important;
padding-left: 60px;
}

#our-services-cards .card {
  text-align: center;
  margin: 40px;
  padding: 0px 30px 30px 30px;
  color: #006699;
  font-size: 30px;
  
}

.service-icon {
  font-size: 50px;
}

.link-btn {
  background-color:#006699;
  border: none;
  border-radius: 5px;
  padding: 12px;
  color: white;
  /* font-weight: bold; */
  font-size: 20px;
  margin: 40px;
}

.link-btn a {
  text-decoration: none !important;
    color: white;
}

.link-btn:hover {
  background-color: #013a57;
  transition: 0.5s ease;
}
